




















import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { LoadingScreen } from "@/mixins/loadingScreen";
import { jobsModule, repositoriesModule, routerModule } from "@/store/modules";
import RepositoriesList from "@/components/repositories/RepositoriesList.vue";
import JobsList from "@/components/jobs/JobsList.vue";

@Component({
  components: {
    RepositoriesList,
    JobsList
  }
})
export default class RepositoryView extends Mixins(LoadingScreen) {
  mounted() {
    this.setLoading(true);
    setTimeout(() => this.setLoading(false), 400);
  }

  get searchQuery() {
    return String(routerModule.query.query) || "";
  }

  strContainsQuery(str: string) {
    console.log(str);
    return str.toLowerCase().includes(this.searchQuery.toLowerCase());
  }

  get jobs() {
    return jobsModule.allJobs.filter(j => this.strContainsQuery(j.name));
  }

  get repositories() {
    return repositoriesModule.allRepositories.filter(r =>
      this.strContainsQuery(r.name)
    );
  }
}
