







import { Component, Vue } from "vue-property-decorator";
import ClientEditor from "@/components/misc/ClientEditor.vue";
import { adminModule } from "@/store/modules";
import { IClient } from "@/shared";
@Component({
  components: {
    ClientEditor
  }
})
export default class NewClientView extends Vue {
  async addClient(client: IClient) {
    const resp = await adminModule.addClient(client);
    if (resp && resp.payload) {
      this.$router.replace({
        path: `/admin/client/${resp.payload.client.id}`
      });
    }
  }
}
