









import { Component, Vue } from "vue-property-decorator";
import { adminModule } from "@/store/modules";
import ClientEditor from "@/components/misc/ClientEditor.vue";
import ClientList from "@/components/clients/ClientsList.vue";

@Component({
  components: {
    ClientEditor,
    ClientList
  }
})
export default class Admin extends Vue {
  async mounted() {
    await adminModule.fetchAllClients();
  }
  get clients() {
    return adminModule.allClients;
  }
}
